.group-icon {
  position: relative;
  width: 57px;
  height: 57px;
}
.bell-icon,
.ellipse-icon {
  position: relative;
  width: 99.38px;
  height: 98.19px;
}
.ellipse-icon {
  border-radius: 50%;
  height: 99.38px;
  object-fit: cover;
}
.group-parent,
.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}
.group-parent {
  width: 222.38px;
  padding: var(--padding-3xs) 0;
}
.header {
  position: absolute;
  width: calc(100% - 252px);
  top: 0;
  right: 3px;
  left: 249px;
  background-color: var(--color-white);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  height: 85px;
  padding: var(--padding-mini) var(--padding-21xl);
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-gray-500);
  font-family: var(--font-roboto-slab);
}
