.toggle,
.toggle-child {
  border-radius: var(--br-8xs);
  border: 1.4px solid var(--color-dimgray-200);
}
.toggle-child {
  position: absolute;
  margin: 0 !important;
  top: 0;
  left: 0;
  background-color: var(--color-white);
  box-sizing: border-box;
  width: 20px;
  height: 42px;
  z-index: 0;
}
.toggle {
  background-color: var(--color-lightgray-200);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
}
.any-studio,
.your-organisation {
  position: relative;
  font-weight: 300;
  z-index: 0;
}
.any-studio {
  font-size: var(--font-size-xl);
  font-weight: 500;
  color: var(--color-gray-900);
  z-index: 1;
}
.bracket-code-angle-programmin-icon {
  position: absolute;
  margin: 0 !important;
  height: 66.72%;
  width: 11.61%;
  top: 20.49%;
  right: -41.65%;
  bottom: 12.79%;
  left: 130.03%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  z-index: 2;
}
.your-organisation-parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
  position: relative;
  gap: var(--gap-7xs);
}
.search-icon {
  position: relative;
  width: 58px;
  height: 64px;
}
.toggle-parent {
  width: 494px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-gray-500);
  font-family: var(--font-roboto-slab);
}
