.need-help {
  margin: 0;
}
.open-our-help {
  margin: 0;
  color: var(--color-dimgray-800);
}
.need-help-open-container {
  flex: 1;
  position: relative;
  line-height: 40px;
  font-weight: 600;
}
.left-nav-panel,
.need-help-open-our-help-cente-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.need-help-open-our-help-cente-wrapper {
  align-self: stretch;
  border-radius: var(--br-5xs);
  flex-direction: row;
  padding: var(--padding-31xl) var(--padding-16xl);
  color: var(--color-ghostwhite);
}
.left-nav-panel {
  position: absolute;
  height: 103.91%;
  width: 17.15%;
  top: -1.95%;
  right: 82.85%;
  bottom: -1.95%;
  left: 0;
  background-color: var(--color-gray-100);
  flex-direction: column;
  gap: var(--gap-89xl);
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-white);
  font-family: var(--font-inter);
}
