@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;500;600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;
  --font-roboto-slab: "Roboto Slab";
  --font-source-sans-pro: "Source Sans Pro";

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-base: 16px;
  --font-size-3xs: 10px;
  --font-size-sm: 14px;
  --font-size-xs: 12px;
  --font-size-lg: 18px;
  --font-size-21xl: 40px;
  --font-size-25xl: 44px;

  /* Colors */
  --color-whitesmoke-100: #f3f6f9;
  --color-whitesmoke-200: #ebf0f1;
  --color-black: #000;
  --color-powderblue-200: #abdde4;
  --color-powderblue-100: rgba(171, 221, 228, 0.6);
  --color-cadetblue: #2ca9bc;
  --color-teal: #1e7683;
  --color-white: #fff;
  --color-gray-400: #778689;
  --color-gray-500: #807b7b;
  --color-gray-600: #77777e;
  --color-gray-700: #677a7d;
  --color-gray-800: #232323;
  --color-gray-100: #1c212d;
  --color-gray-200: #212121;
  --color-gray-900: #070707;
  --color-gray-300: rgba(119, 119, 126, 0.1);
  --color-gray-1000: rgba(119, 119, 126, 0.2);
  --color-dimgray-500: #777272;
  --color-dimgray-100: #696a77;
  --color-dimgray-600: #6f6767;
  --color-dimgray-700: #636470;
  --color-dimgray-200: #595e5f;
  --color-dimgray-800: #4f5768;
  --color-dimgray-300: rgba(105, 106, 119, 0.4);
  --color-dimgray-900: rgba(105, 106, 119, 0.3);
  --color-dimgray-1000: rgba(111, 112, 124, 0.5);
  --color-dimgray-400: rgba(105, 106, 119, 0.6);
  --color-lightgray-200: rgba(199, 207, 215, 0.6);
  --color-lightgray-100: rgba(194, 205, 215, 0.4);
  --color-lightgray-300: rgba(201, 204, 207, 0.2);
  --color-lightseagreen: #0ba6b0;
  --color-darkorange-200: #f69829;
  --color-darkorange-100: #ff8300;
  --color-darkorange-300: rgba(246, 152, 41, 0.6);
  --color-slategray-100: #555275;
  --color-slategray-200: rgba(103, 128, 132, 0.4);
  --color-darkgray-200: #b0b5b6;
  --color-darkgray-100: #a29c9c;
  --color-darkgray-300: rgba(175, 179, 179, 0.6);
  --color-silver-100: #c3c7c8;
  --color-silver-200: #b6b6b6;
  --color-silver-300: rgba(180, 183, 184, 0.6);
  --color-darkslategray: #3d3b3b;
  --color-ghostwhite: #f5f7fb;

  /* Gaps */
  --gap-11xl: 30px;
  --gap-6xl: 25px;
  --gap-4xs: 9px;
  --gap-55xl: 74px;
  --gap-221xl: 240px;
  --gap-xs: 12px;
  --gap-4xl: 23px;
  --gap-xl: 20px;
  --gap-mid: 17px;
  --gap-7xs: 6px;
  --gap-3xs: 10px;
  --gap-8xs: 5px;
  --gap-2xs: 11px;
  --gap-mini: 15px;
  --gap-base: 16px;
  --gap-2xl: 21px;
  --gap-38xl: 57px;
  --gap-89xl: 108px;

  /* Paddings */
  --padding-base: 16px;
  --padding-xl: 20px;
  --padding-3xs: 10px;
  --padding-11xl: 30px;
  --padding-mini: 15px;
  --padding-mid: 17px;
  --padding-21xl: 40px;
  --padding-9xs: 4px;
  --padding-16xl: 35px;
  --padding-53xl: 72px;
  --padding-11xs: 2px;
  --padding-41xl: 60px;
  --padding-481xl: 500px;
  --padding-xs: 12px;
  --padding-5xs: 8px;
  --padding-8xs: 5px;
  --padding-7xs: 6px;
  --padding-57xl: 76px;
  --padding-31xl: 50px;

  /* Border radiuses */
  --br-5xs: 8px;
  --br-xl: 20px;
  --br-7xs: 6px;
  --br-31xl: 50px;
  --br-11xs: 2px;
  --br-9xs: 4px;
  --br-10xs: 3px;
  --br-8xs: 5px;
  --br-3xs: 10px;
}
