.hello-here-is {
  margin: 0;
}
.hello-here-is-container {
  position: relative;
}
.message-1 {
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
  box-shadow: 6px 6px 21px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-base) var(--padding-xl);
}
.frame-child6,
.new-file-empty-common-file-co-icon {
  position: relative;
  width: 21.79px;
  height: 25.42px;
}
.frame-child6 {
  border-top: 1px solid var(--color-gray-1000);
  box-sizing: border-box;
  width: 158.01px;
  height: 1px;
}
.c-v-newdoc-container {
  position: relative;
  letter-spacing: -0.04em;
}
.arrow-down-dashed-square-arro-icon {
  position: relative;
  width: 17px;
  height: 17px;
}
.c-v-newdoc-12-mb-parent,
.line-container,
.new-file-empty-common-file-co-parent {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-55xl);
}
.line-container,
.new-file-empty-common-file-co-parent {
  flex-direction: column;
  gap: var(--gap-4xs);
}
.new-file-empty-common-file-co-parent {
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
  box-shadow: 6px 6px 25px rgba(0, 0, 0, 0.25);
  justify-content: center;
  padding: var(--padding-mini) var(--padding-xl);
  gap: var(--gap-6xl);
  font-size: var(--font-size-3xs);
}
.message-1-parent {
  width: 471px;
  height: 249.64px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-11xl);
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-black);
  font-family: var(--font-inter);
}
